import styled from "styled-components";
function Intro() {
  return (
    <Wrap id="home-card">
      {/*Card - Started*/}
      <div className="card-inner card-started active">
        {/* Started Background */}
        <div
          id="particles-bg"
          className="slide"
          style={{
            backgroundColor: "#090b06",
            // backgroundImage: `url(images/bg.jpg)`,
          }}
        ></div>

        <div className="centrize full-width">
          <Content>
            {/* <Photo src="images/profile-photo.jpg" alt="Sharooq Salaudeen" /> */}

            {/* Started titles */}
            <div className="title">
              <span>Sharooq</span> Salaudeen
            </div>
            <div className="subtitle">
              I&nbsp;
              <div className="typing-title">
                <p>
                  build <strong>Full stack web applications.</strong>
                </p>
                <p>
                  create <strong>games on Unity Engine .</strong>
                </p>
                <p>
                  write <strong>Machine Learning Algorithms</strong>
                </p>
              </div>
              <span className="typed-title"></span>
            </div>
          </Content>
        </div>
      </div>
    </Wrap>
  );
}

export default Intro;

const Wrap = styled.div`
  padding-top: 100px;
  margin-bottom: 130px;
  max-height: 450px;
`;

// const Photo = styled.img`
