export function downloadGoogleDocAsPDF(docUrl) {
  // Replace the ending part of Google Docs URL with 'export?format=pdf'
  const pdfUrl = docUrl.replace(/\/edit.*$/, "/export?format=pdf");

  // Create a link element to trigger the download
  const downloadLink = document.createElement("a");
  downloadLink.href = pdfUrl;
  downloadLink.download = "document.pdf";

  // Append the link to the document body and click the link to start the download
  document.body.appendChild(downloadLink);
  downloadLink.click();

  // Remove the link after the download starts
  document.body.removeChild(downloadLink);
}
